export function getWishlistById(
  httpClient: HttpClient
): GetWishlistByIdSignature {
  return (_id: string, options?: GetWishlistByIdOptions) =>
    universalGetWishlistById(
      _id,
      options,
      // @ts-ignore
      { httpClient }
    );
}

interface GetWishlistByIdSignature {
  /**
   * Get wishlist by id
   * @param - Unique identifier representing requested list
   * @returns Object containing requested list data
   */
  (_id: string, options?: GetWishlistByIdOptions): Promise<
    NonNullablePaths<
      WishlistData,
      | `ownerId`
      | `items`
      | `items.${number}._id`
      | `items.${number}.type`
      | `items.${number}.origin`
      | `totalCount`,
      4
    >
  >;
}

